<template>
    <div class="ajax-loading" id="ajaxLoading">
      <div class="overlay"></div>
      <div class="loading">
          <img src="../../pictures/gif/loading.gif" alt="loading">
          <span>加载中，请稍后...</span>
      </div>
    </div>
</template>

<script>
export default{
    name: 'Loading'
}
</script>

<style scoped>
.ajax-loading .loading{
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    padding:0 40px;
    height: 80px;
    line-height: 80px;
    background: rgba(0, 0, 0, 0.75);
    border-radius: 6px;
    text-align: center;
    z-index: 9999;
    font-size: 14px;
    color:#fff;    
}

.ajax-loading .loading img{
    width: 32px;
    vertical-align: middle; 
}

.ajax-loading .loading span{
    width: 32px;
    vertical-align: middle;    
}

.ajax-loading .overlay{
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 9998;
    background: rgb(255, 255, 255);
    opacity: 0.1;    
}

</style>


